


/* Button Sizes */
.btn {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    letter-spacing: 1px;
    width: 100%;
    height: 56px;
    background-color: var(--white);
}
.black_btn {
    border: 1px solid var(--green-900);
    color: var(--green-900);
}
.orange_btn,
.black_btn:focus, 
.black_btn:hover,
.white_btn:focus,
.white_btn:hover
 {
    border: 1px solid var(--orange-400);
    background-color: var(--orange-400);
    color: var(--green-900);
}
.orange_btn:focus, 
.orange_btn:hover
 {
    border: 1px solid var(--green-900);
    background-color: var(--green-900);
    color: var(--white);
}
.orange_btn:disabled{
    background: var(--green-50);
border: 1px solid var(--green-50);
color: var(--green-300);
}

