
* {
  box-sizing: border-box;
  font-family: Helvetica Neue, sans-serif;
  margin: 0;
  padding: 0;
  text-decoration: none
}

.Aboutus_banner > div,
.aboutus_content > div,
.aboutus_top > div,
.case-banner > div,
.case_study,
.contactus>div > div,
.footer>div > div,
.home .grids,
.home .heading > div,
.home>.BigHeader_container > div,
.home>.buttons,
.navbar,
.service > div,
.works > div >div{
  margin: 0 auto;
  max-width: 1440px;
  padding: 80px 16px;
  width: 100%
}

.service .heading {
  padding-bottom: 80px
}
.home .grids{
  padding: 0 16px;
}

@media (min-width:576px) {

  .Aboutus_banner > div,
  .aboutus_content > div,
  .aboutus_top > div,
  .case-banner > div,
  .case_study,
  .contactus>div>div,
  .footer>div > div,
  .home .grids,
  .home .heading > div,
  .home>.BigHeader_container > div,
  .home>.buttons,
  .navbar,
  .service > div,
  .works>div>div {
      padding: 80px 24px
  }
  .home .grids{
    padding: 0 24px;
  }
}

@media (min-width:1024px) {

  .Aboutus_banner > div,
  .aboutus_content > div,
  .aboutus_top > div,
  .case-banner > div,
  .case_study,
  .contactus>div>div,
  .footer>div > div,
  .home .grids,
  .home .heading > div,
  .home>.BigHeader_container > div,
  .home>.buttons,
  .navbar,
  .service > div,
  .works>div>div {
      padding: 100px 32px
  }

  .home .grids{
    padding: 0 32px;
  }
  .service .heading {
      padding-bottom: 100px
  }
}

@media (min-width:1280px) {

  .Aboutus_banner > div,
  .aboutus_content > div,
  .aboutus_top > div,
  .case-banner > div,
  .case_study,
  .contactus>div>div,
  .footer>div > div,
  .home .grids,
  .home .heading > div,
  .home>.BigHeader_container > div,
  .home>.buttons,
  .navbar,
  .service > div,
  .works>div>div {
      padding: 100px 144px
  }
  .home .grids{
    padding: 0 144px;
  }
}
